* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul {
  padding: 0;
  margin: 0;
  li {
    list-style: none;
  }
}

p {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

footer {
  position: relative;
  width: 100%;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  color: $black;
  @include roboto-regular;
  font-size: rem(16px);
  line-height: 1.42;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @include phones{
    font-size: rem(14px);
  }

}

img {
  display: block;
  width: 100%;
  max-width: 100%;
}

blockquote {
  color: #555;
  font-size: .9em;
  padding: 20px;
  background: #f3f3f3
}

blockquote cite {
  font-size: .9em
}