.header {
	display: flex;
	width: 100%;
	background: $dark-violet;
	&__container {
		padding: 15px 0;
		display: flex;
		justify-content: space-between;
		margin: auto;
		width: 95%;
		max-width: 1200px;
		min-width: 320px;
		@include tablets{
		  align-items: center;
		}
		@include phones {
			justify-content: space-around;
			height: 160px;
			flex-direction: column;
		}
	}
	&__right-row {
		position: relative;
		display: flex;
		justify-content: flex-end;
		margin: rem(15px) 0 0 0;
		&:first-child {
			margin: 0;
		}
		@include tablets {
			margin: 0;
		}
		@include tablets__h {
			margin: 0;
		}
	}
	&__serch {
		position: relative;
		padding: 0 rem(25px) 0 0;
	}
	&__serch-icon {
		width: 25px;
		height: 25px;
		fill: $white;
	}
}
.header__left {
	position: relative;
	width: 20%;
	display: flex;
	right: 16px;
	@include phones {
		position: relative;
		display: flex;
		justify-content: center;
		width: 100%;
	}
	@include tablets{
	  width: 100%;
	  right: 0;
	}
}
.header__right {
	width: 80%;
	@include desktop {
		display: flex;
		justify-content: flex-end;
	}
	@include phones {
		display: flex;
		justify-content: center;
		width: 100%;
	}
}
.logo {
	width: 185px;
	height: 50px;
	@include tablets{
	  width: 80%;
	}
	@include phones {
		width: 60%;
		height: auto;
	}
}
.nav 
{

	&__list {
		display: flex;
		white-space: nowrap;
		justify-content: flex-end;
		@include grid-col(12 12 12 12);
	}

	&__item {
		display: flex;
		@include desktop {
			padding: 0 rem(5px);
			&:last-child {
				padding: 0;
			}
		}
		&:after {
			content: '|';
			color: $white;
			padding: 0 rem(5px);
			@include desktop {
				content: none;
			}
		}
	}
	&__link {
		color: $white;
		&:hover {
			color: $yellow;
		}
	}
}
.nav__item:last-child:after {
	content: none;
	display: none;
}
.social {
	&__list {
		display: flex;
	}
	&__item {
		padding: 0 rem(15px) 0 0;
		&:last-child {
			padding: 0;
		}
	}
	&__icon {
		width: 25px;
		height: 25px;
		fill: $black;
		background: $white;
		padding: rem(5px);
	}
}